/* Full page background color */
.page {
  position: relative;
  background-color: #46c5bf;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-x: hidden;
}

/* NAVIGATION BAR */
.navbar {
  width: 100%;
  background-color: #00b6b6;
  padding: 1rem;
  position: relative;
  z-index: 10;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

.navbar ul {
  list-style: none;
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin: 0;
  padding: 0;
}

.navbar a {
  color: rgb(216, 230, 226);
  text-decoration: none;
  font-weight: bold;
  font-size: 1.1rem;
}

.navbar a:hover {
  text-decoration: underline;
}

/* Background image top */
.hero-image {
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center top;
  border-radius: 1rem;
  height: 300px;
  width: 100%;
  margin-bottom: 1rem;
}

/* Header box */
.header {
  text-align: center;
  background-color: #62c9c4;
  color: rgb(216, 230, 226);
  padding: 2rem 1rem;
  border-radius: 1rem;
  max-width: 800px;
  width: 90%;
  box-shadow: 0 8px 16px rgba(14, 5, 65, 0.1);
  margin-top: 1rem;
  margin-bottom: 2rem;
}

.header h1 {
  font-size: 2.5rem;
  margin: 0;
}

.header p {
  font-size: 1rem;
  margin-top: 0.5rem;
}

/* Main section */
.main {
  display: flex;
  justify-content: center;
  padding: 2rem 1rem;
  width: 100%;
  flex-wrap: wrap;
}

/* Service Cards Layout */
.service-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
  gap: 1.5rem;
  padding: 1rem;
  width: 100%;
  max-width: 1000px;
  margin-bottom: 2rem;
  z-index: 1;
  position: relative;
}

/* Individual Card */
.card {
  flex: 1 1 200px;
  max-width: 250px;
  height: 200px;
  text-decoration: none;
  perspective: 1000px;
}

.card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}

.card:hover .card-inner {
  transform: rotateY(180deg);
}

.card-front,
.card-back {
  position: absolute;
  inset: 0;
  backface-visibility: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 12px;
  background-color: #004e50;
  overflow: hidden;
}

.card-back {
  transform: rotateY(180deg);
}

.card-front img,
.card-back img {
  width: 80%;
  height: auto;
}

/* Footer */
.footer {
  background-color: #00796b;
  color: white;
  text-align: center;
  padding: 1rem;
  width: 100%;
  margin-top: 2rem;
  border-radius: 1rem;
}

/* Bubble Animation */
.bubbles {
  position: absolute;
  inset: 0;
  overflow: hidden;
  z-index: 0;
  pointer-events: none;
}

.bubble {
  position: absolute;
  bottom: -60px;
  background: rgba(255, 255, 255, 0.4);
  border-radius: 50%;
  animation: floatUp 10s linear infinite;
}

.bubble::after {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: radial-gradient(circle, white 20%, transparent 70%);
}

.bubble:nth-child(odd) {
  width: 15px;
  height: 15px;
  left: 10%;
  animation-duration: 9s;
}
.bubble:nth-child(even) {
  width: 25px;
  height: 25px;
  left: 30%;
  animation-duration: 12s;
}
.bubble:nth-child(3n) {
  width: 20px;
  height: 20px;
  left: 50%;
  animation-duration: 10s;
}
.bubble:nth-child(4n) {
  width: 18px;
  height: 18px;
  left: 70%;
  animation-duration: 11s;
}
.bubble:nth-child(5n) {
  width: 30px;
  height: 30px;
  left: 85%;
  animation-duration: 14s;
}

@keyframes floatUp {
  0% {
    transform: translateY(0);
    opacity: 0.6;
  }
  100% {
    transform: translateY(-100vh);
    opacity: 0;
  }
}

/* Social links */
.social-links {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 2rem;
}

.social-icon {
  width: 40px;
  height: 40px;
  transition: transform 0.3s ease;
  background-color: rgb(185, 208, 209);
  border-radius: 1rem;
}

.social-icon:hover {
  transform: scale(1.1);
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  .service-cards {
    flex-direction: column;
    align-items: center;
  }

  .card {
    width: 90%;
    max-width: 300px;
    height: auto;
  }

  .card-inner {
    aspect-ratio: 1 / 1;
  }
}
